a {
  text-decoration: none;
}



.top {
  margin-top: 60px;
}

.top ul {
  padding: 0;
  margin: 0;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 20px;
  height: 60px;
  opacity: 0.9;
  z-index: 10;
}



ul {
  box-sizing: border-box;
}

.top ul::after {
  content: '';
  display: block;
  clear: both;
}

.top ul .item {
  flex-grow: 1;
}

.logo {
  background: url('/src/leenotelogo.png') no-repeat top center;
  background-size: 100%;
  width: 30%;
  height: 30%;
  min-width: 60px;
  max-width: 80px;
  max-height: 50px;
  line-height: 30%;
}

.searchBar {
  text-align: center;
  width: 100%;
  box-sizing: border-box;
}

.searchBar input {
  border: 0px;
  width: 70%;
  height: 34px;
  background: #eee;
  padding: 2px 10px;
  margin-right: 10px;
  border-radius: 100px;
  box-sizing: border-box;
}

.searchBar input:focus {
  outline: none;
}

.sideMenu {
  text-align: right;
}