.wrap {
  padding: 0 20px;
  box-sizing: border-box;
}

.note-card {
  width: 100%;
}

.note-card ul {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  opacity: 0.9;
}


.note-card ul li {
  width: 48%;
  height: 150px;
  background: white;
  margin: 1%;
  box-sizing: border-box;
  border-radius: 16px;
  box-shadow: 0 6px 6px 6px #eee;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
}

h1,
p {
  font-size: 14px;
  font-weight: 300;
}

h1 {
  font-size: 16px;
  font-weight: 400;
}

p.content {
  width: 80%;
  padding: 0 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

p.date {}